@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0px;
  padding:0px;
  font-family: 'Rubik', sans-serif;
}

.App {
  text-align: center;
  height:100vh;
}

.app-title{
  color: #fff;
  font-weight:700;
  font-size:1.8rem;
  padding-top:1.4em;
}

.error{
  color:red;
  font-weight:700;
  margin-top:0.5rem;
}

.heading-holder{
  height:35vh;
  background-image: url(/ip-address-tracker/static/media/pattern-bg.a60a3c85.png);
}

.info-absolute{
  position: absolute;
  top:25%;
  width: 100vw;
}

.info-holder{
  position:relative;
  background-color:white;
  border-radius:10px;
  width:90vw;
  min-height:20vh;
  display:flex;
  justify-content:center;
  margin:auto;
  z-index:10;
}

button{
  font-size:18px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  padding:1.1rem;
  border-width: 0px;
  background:black;
  color:white;
}

button:hover{
  cursor: pointer;
}

input{
  font-size:18px;
  width: 30vw;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  padding:1.1rem;
  border-width: 0px;
}

.input-holder{
  margin-top:3vh;
}

.leaflet-map{
  height:65vh;
  background:wheat;
}

.leaflet-container{
  height:100%;
  z-index:1;
}

.leaflet-control-container{
  z-index: 1000;
}

.spinner{
  margin:auto;
}

@media(max-width:950px){
  .info-holder{
    flex-direction:column;
    align-items:center;
    width: 70vw;
    min-height:0vh;
  }
}

.info-item-holder{
    background-color:white;
    width:18vw;
    text-align:left;
    margin-right:0vw;
    margin-left:2vw;
    margin-top:2.2rem;
    margin-bottom:2.2rem;
    border-right: solid 1px hsl(0, 0%, 85%);
}

.info-item-holder:last-child{
    border-right:none;
}

.info-item-description{
    color:hsl(0, 0%, 59%);
    margin-bottom:1rem;
    font-weight: 700;
    font-size:0.8rem;
    letter-spacing: 0.1rem;
}

.info-item-text{
    font-size:1.6rem;
    color:black;
    font-weight:700;
}

@media(max-width:950px){
    .info-item-holder{
        margin-top: 2vh;
        margin-bottom: 0px;
        margin-left: 0px;
        margin-right: 0px;
        border: none;
        text-align: center;
        width:100%;
    }

    .info-item-description{
        margin-bottom:0.5rem;
        font-size:0.6rem;
    }

    .info-item-text{
        font-size:1rem;
    }

    .info-item-holder:last-child{
        margin-bottom: 2vh;
    }
  }
